.connect {
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.connect .padding {
    -webkit-flex-grow: 1;
            flex-grow: 1;
}
.connect > div:not(.padding) {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}
.connect form {
    border: 1px solid #FFFFFF;
    border-radius: 2px;
    background-color: #DDDDDD;
    color: #000000;
    box-shadow: #000000 3px 1px 10px;
    padding: 10px;
    min-width: 400px;
}

.data-view {
    border: 3px solid #000000;
    position: absolute;
    position: block;
    background-color: #333333;
}
.data-view > .handle {
    position: absolute;
    width: 4px;
    height: 4px;
    top: 50%;
    left: 50%;
    margin-top: -3px;
    margin-left: -3px;
    border: 1px solid #000000;
}
.data-view > .handle:hover, .data-view > .handle:active {
    border-color: #DDDDFF;
}
.data-view > .handle-top {
    margin-top: -4.5px;
    top: 0;
    cursor: n-resize;
}
.data-view > .handle-left {
    margin-left: -4.5px;
    left: 0;
    cursor: w-resize;
}
.data-view > .handle-bottom {
    margin-top: -1.5px;
    top: 100%;
    cursor: s-resize;
}
.data-view > .handle-right {
    margin-left: -1.5px;
    left: 100%;
    cursor: e-resize;
}
.data-view > .handle-top.handle-left {
    cursor: nw-resize;
}
.data-view > .handle-top.handle-right {
    cursor: ne-resize;
}
.data-view > .handle-bottom.handle-left {
    cursor: sw-resize;
}
.data-view > .handle-bottom.handle-right {
    cursor: se-resize;
}
.data-view > .container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
}

.context-menu {
    position: fixed;
    width: 100px;
    background-color: #FFFFFF;
    z-index: 1;
    list-style-type: none;
    margin: 0;
    padding: 0;
}
.context-menu li {
    border: 1px solid #777777;
    color: #000000;
    height: 20px;
    box-sizing: border-box;
}
.context-menu li:hover {
    background-color: #CCCCCC;
}

.label-edit-area {
    width: 100%;
    height: 100%;
}
.label-edit-container {
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.label {
    text-align: center;
}

.graph-container {
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.graph-canvas {
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.graph-container textarea {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    resize: none;
}

.grid-dot {
    width: 2px;
    height: 2px;
    margin-left: -2px;
    margin-top: -2px;
    position: absolute;
    border: 1px solid #777777;
}
.main {
    width: 100%;
    height: 100%;
}

.button-view {
    width: 100%;
    height: 100%;
    text-align: center;
}
.button-view:hover {
    background-color: #444644;
}
.button-view:active {
    background-color: #555555;
}
.button-view p {
    margin: 0;
    padding: 10px;
}

.text-input-area {
    width: 100%;
    height: 100%;
}
.text-input-container {
    width: 100%;
    height: 100%;
    overflow: hidden;
}

body {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}
.app {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #333333;
    color: #FFFFFF;
    font-family: 'Muli', sans-serif;
}
h1 {
    text-align: center;
}
.right {
    float: right;
}
.input-group:not(.checkbox) > label {
    font-size: 0.7em;
    line-height: 0.7em;
    margin-left: 20px;
}
.input-group:not(.checkbox) > input {
    background-color: transparent;
    border: none;
    border-bottom: 1px solid #777777;
    width: calc(100% - 10px);
    box-shadow: none;
    padding: 0 5px;
}
.input-group:not(.checkbox) > input:active, .input-group:not(.checkbox) > input:focus {
    border-color: #003F00;
}
.input-group:not(.checkbox) > input:invalid {
    border-color: #7F0000;
}

